.contacts {
  min-height: 100vh;
  padding: 0 0 100px 0;
  background-color: #eaedf2;
  display: block;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;

}

.container {
  /*height: 100%;*/
  /*width: 80%;*/
  /*border: 1px solid;*/
  /*margin: 0 auto;*/
  /*display: flex;*/
  /*flex-direction: column;*/
  /*align-items: center;*/
  padding-right: 15px;
  padding-left: 15px;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;
  height: 100%;
  width: 80%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;

}


.row {
  justify-content: center !important;
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;

}


.column {
  flex: 0 0 66.666667%;
  max-width: 66.666667%;
  margin-top: 15px;
  margin-bottom: 15px;
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;
  text-align: left;
  @media screen and (max-width: 992px) {
    max-width: 100%;
    flex: 100%;
    flex-wrap: nowrap;
  }


}

.contactForm {
  border-radius: 5px;
  background: #ffffff;
  padding: 1% 5% 5% 5%;
  box-shadow: 0px 3px 10px 0px rgba(38, 59, 94, 0.1);
  display: block;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;

}


.title {
  font-weight: 600;
  font-size: 18px;
  text-transform: uppercase;
  color: #090a0c;
  margin-bottom: 20px;
  font-family: "Poppins", sans-serif;
}

.rowForm {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
  margin-top: 0em;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}

.formGroup {
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  display: block;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;
  text-align: left;
  flex: 0 0 50%;
  max-width: 50%;
  @media screen and (max-width: 1129px) {
    flex: 100%;
    max-width: 100%;
  }


}


.colMd {
  position: relative;
  margin-bottom: 20px;
  display: block;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;


}


.formControl {
  border: none;
  border-bottom: 1px solid #ccc;
  border-radius: 0;
  box-shadow: none;
  font-size: 14px;
  display: block;
  width: 100%;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  margin: 0;
  font-family: "Poppins", sans-serif;


}

textarea.formControl {
  overflow: auto;
  resize: vertical;
  height: 80px;
  margin: 0;


}

.formControl:focus {
  outline: none;
  border-bottom: 1px solid #d9832e;

}


.line {
  position: absolute;
  bottom: -1px;
  left: auto;
  right: 0;
  width: 0px;
  height: 2px;
  transition: ease all 0.35s;
  background-color: #d9832e;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;
}


.formGroup2 {
  flex: 0 0 100%;
  max-width: 100%;
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;

}


.formGroup3 {
  flex: 0 0 100%;
  max-width: 100%;
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;

}

.mBtn {
  background: #d9832e;
  color: #ffffff;
  cursor: pointer;
  display: inline-block;
  border: 2px solid transparent;
  line-height: inherit;
  border-radius: 0;
  width: auto;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  transition: ease-in-out all 0.55s;
  text-align: center;
}


.column2 {
  margin-top: 15px;
  margin-bottom: 15px;
  flex: 0 0 33.333333%;
  max-width: 33.333333%;
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;
  flex-wrap: nowrap;
  @media screen and (max-width: 992px) {
    max-width: 100%;
    flex: 100%;
  }


}


.info {
  background: #ffffff;
  padding: 21px;
  box-shadow: 0px 3px 10px 0px rgba(38, 59, 94, 0.1);
  display: flex;
  align-items: flex-start;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;
  text-align: left;
  width: 300px;

  @media screen and (max-width: 992px) {
width: 100%;
  }


}

.info:not(:first-child) {
  margin-top: 20px;
}


.icon {
  width: 40px;
  height: 40px;
  text-align: center;
  border-radius: 50%;
  color: #ffffff;
  background: #d9832e;
  font-size: 19px;
  line-height: 40px;
  margin-right: 20px;
  display: block;
}


.image {
  width: 50%;


}


.mediaBody {
  flex: 1;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  color: #555a64;
  font-weight: 400;
  line-height: 1.6;
  text-align: left;


}


.topic {
  font-size: 15px;
  font-weight: 500;
  color: #090a0c;
  margin-bottom: 8px;
  margin-top: 0;
  display: block;
  line-height: 1.2;

}


.text {
  font-size: 14px;

}


