
.mainBlock {
  min-height: 80vh;
  background-color: #ffffff;
  margin-top: 160px;
  padding-bottom: 30px;
  @media screen and (max-width: 992px) {
    margin-top: 120px;
  }


  .mainText {
    width: 50%;
    font-weight: 400;
    color: #555a64;
    line-height: 1.6;
    font-family: "Poppins", sans-serif;

    @media screen and (max-width: 842px) {
      width: 100%;


    }

    .title {
      color: #090a0c;
      font-size: 18px;
      margin: 0 0 15px;

    }

    .name {
      font-size: 50px;
      color: #090a0c;
      font-weight: 600;
      margin: 0 0 15px;
      line-height: 1;
      @media screen and (max-width: 767px) {
        font-size: 35px;
        margin: 10px 0 15px;
      }
    }

    .profession {
      font-weight: 500;
      font-size: 20px;
      color: #090a0c;
      margin: 0 0 15px;
      @media screen and (max-width: 767px) {
        font-size: 16px;

      }
    }

    .text {
      color: rgba(9, 10, 12, 0.8);
      margin: 0;
    }

    .btnBar {
      padding: 25px 0;
      display: block;
      font-family: "Poppins", sans-serif;
      font-size: 15px;
      color: #555a64;
      font-weight: 400;
      line-height: 1.6;
      background-color: #fff;

      .work {
        min-width: 120px;
        margin-right: 12px;
        background: #d9832e;
        color: #ffffff;
        display: inline-block;
        border: 2px solid transparent;
        line-height: inherit;
        border-radius: 0;
        width: auto;
        padding: 10px 20px;
        font-size: 14px;
        cursor: pointer;
        font-weight: 500;
        text-decoration: none;
      }

      .here {
        min-width: 120px;
        margin-right: 12px;
        background: transparent;
        color: #d9832e;
        display: inline-block;
        border: 2px solid #d9832e;
        line-height: inherit;
        border-radius: 0;
        width: auto;
        padding: 10px 20px;
        font-size: 14px;
        cursor: pointer;
        font-weight: 500;
        text-transform: uppercase;
        transition: ease-in-out all 0.55s;
        text-align: center;
        text-decoration: none;
        font-family: "Poppins", sans-serif;
      }
    }
  }

  .photo {
    border: #d9832e 2px solid;
    width: 300px;
    height: 400px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 2px;
    @media screen and (max-width: 842px) {
      width: 400px;
      height: 500px;
      margin-bottom: 90px;
    }


  }

}




